@import url('https://fonts.googleapis.com/css?family=Poiret+One|Tajawal');

body {
  font-family: 'Tajawal', sans-serif;
  margin: 0;
  padding: 0;
  font-weight: 100;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.chennaibg{
  width: 351px;
}

p, h3, h4, h2, h1, h5 {
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
#root {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.topBar {
  display: none;
}

/* Loading CSS */

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
  position: relative;
  top: 30vh;
  left: 15vw;
}

.lds-dual-ring:after {
  width: 150px !important;
  height: 150px !important;
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.LoadHolder {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
}

.LoadingText {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: auto;
  text-align: center;
  font-size: -webkit-xxx-large;
  color: #fd4445;
}

.loaderTweak {
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #fd4445;
  height: 100%;
  width: 100%;
  -webkit-box-shadow: -3px 0px 2px 3px rgba(0, 0, 0, 0.26);
          box-shadow: -3px 0px 2px 3px rgba(0, 0, 0, 0.26);
}

@media only screen and (max-width: 450px) {

  .LoadHolder{
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }

  .LoadingText{
    position: relative;
    top: 15vh;
  }

  .lds-dual-ring{
    top: 15vh;
    left: 30vw;
  }

}


